.overlay {
  width: 0%;
  height: 100%;
  background-color: rgba(3, 127, 255, 0.2);
  position: absolute;
}
/* test navlink */
.listLinkActive{
  background: red;
}
.listItem {
  padding-left: 0px !important;
  padding-right: 0px !important;
}
.listItem:hover .overlay {
  width: 100%;
  transition: 0.75s;
}
.listItemText {
  color: #a4acc4;
}
.listItem:hover .listItemText {
  color: #007bff !important;
  transition: 0.5s;
}

.line {
  background-color: rgba(46, 52, 78, 0.3);
  width: 1px;
  height: 100%;
  position: absolute;
  z-index: -1;
}
.line1 {
  right: 20%;
}
.line2 {
  right: 40%;
}
.line3 {
  right: 60%;
}
.line4 {
  right: 80%;
}
